import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import AtraparPokemon from "@/views/AtraparPokemon.vue";
import Entrenador from "@/views/Entrenador.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/atraparpokemon",
    name: "AtraparPokemon",
    component: AtraparPokemon,
  },
  {
    path: "/:n_entrenador",
    name: "Entrenador",
    component: Entrenador,
  },
  
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
