<template>
  <div>
    <b-form class="form">
      <!-- Primer input -->
      <b-form-group
        id="nombrePokemon"
        label="Nombre Pokemon"
        label-for="nombrePokemon"
        description=""
        class="fuente"
      >
        <b-form-input
          id="nombrePokemon"
          v-model="pokemon.pokeNombre"
          @keyup="buscarPokemonApi()"
          type="text"
          placeholder="Ingresa nombre del pokemon"
          class="mb-3 elemento"
        ></b-form-input>
      </b-form-group>

      <!-- Segundo input -->
      <b-form-group
        id="entrenadorPokemon"
        label="Entrenador Pokemon"
        label-for="entrenadorPokemon"
        class="fuente"
      >
        <b-form-select
          id="entrenadorPokemon"
          v-model="pokemon.selected"
          :options="pokemon.options"
          class="entrenadorPokemon mb-3 fuente elemento"
          required          
        ></b-form-select>
      </b-form-group>

      <!-- Tercer input -->
      <b-form-group
        id="imagenPokemon"
        label="Imagen Pokemon"
        label-for="imagenPokemon"
        class="fuente"
        v-show="false"
      >
        <b-form-input
          id="imagenPokemon"
          v-model="pokemon.pokeImagen"
          type="text"
          placeholder="Ingrese Imagen (Formato URL)"
          class="mb-3 elemento"
          required
          v-show="false"
        ></b-form-input>
      </b-form-group>

  <!--  input esconidos-->
      <b-form-group
        id="movimiento"
        label="movimiento"
        label-for="movimiento"
        class="fuente"
        v-show="false"
      >
        <b-form-input
          id="movimiento"
          v-model="pokemon.move"
          type="text"
          class="mb-3 elemento"
          v-show="false"
          required
        ></b-form-input>
      </b-form-group>
      <!--  input esconidos-->
      <b-form-group
        id="altura"
        label="altura"
        label-for="altura"
        class="fuente"
        v-show="false"
      >
        <b-form-input
          id="altura"
          v-model="pokemon.altura"
          type="text"
          class="mb-3 elemento"
          v-show="false"
          required
        ></b-form-input>
      </b-form-group>
      <!--  input esconidos-->
      <b-form-group
        id="peso"
        label="peso"
        label-for="peso"
        class="fuente"
        v-show="false"
      >
        <b-form-input
          id="peso"
          v-model="pokemon.peso"
          type="text"
          class="mb-3 elemento"
          required
          v-show="false"
        ></b-form-input>
      </b-form-group>
      <!--  input esconidos-->
      <b-form-group
        id="habilidad"
        label="habilidad"
        label-for="habilidad"
        class="fuente"
        v-show="false"
      >
        <b-form-input
          id="peso"
          v-model="pokemon.habilidad"
          type="text"
          class="mb-3 elemento"
          required
          v-show="false"
        ></b-form-input>
      </b-form-group>

        <!--  input esconidos-->

      <!-- Cuarto input -->
      <b-form-group
        id="datosPokemon"
        label="Datos del Pokemon"
        label-for="datosPokemon"
        class="fuente"
      >
        <b-form-input
          id="datosPokemon"
          v-model="pokemon.pokeDatos"
          type="text"
          placeholder="Ingrese algunos datos de su querido Pokemon"
          class="elemento"
          required
        ></b-form-input>
      </b-form-group>
    
      
      <b-button
        id="boton"
        type="button"
        variant="dark"
        @click="agregarPokeAction()"
        class="mt-4 fuente elemento"
        >Atrapalo ya!</b-button
      >
    </b-form>
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from "vuex";
export default {
  name: "Formulario",
  computed: {
    ...mapState(["pokemon"]),
    ...mapGetters(["armarURLPokemon"]),
  },
  methods: {
    ...mapActions(["agregarPokeAction"]),
    buscarPokemonApi: async function () {
      const data = await fetch(this.armarURLPokemon);
      const pokemon = await data.json();
      let { id, height, weight, moves, abilities } = pokemon;
      let movimiento = moves[0].move.name;
      let habilidad = abilities[0].ability.name;
      let peso = (this.pokemon.peso = weight / 10);
      this.pokemon.pokeImagen = id;
      this.pokemon.altura = height;
      this.pokemon.peso = peso;
      this.pokemon.move = movimiento;
      this.pokemon.habilidad = habilidad;
    },
  },
};
</script>

