<template>
  <div>
    <b-container>
      <b-row class="mt-5 pt-5">
        <h1 class="titulo">Pokemones de {{ entrenador }}</h1>
      </b-row>
      <PokemonAtrapado
        v-for="(item, index) in arrayFiltrado"
        :key="index"
        :nombrePoke="arrayFiltrado[index].nombre"
        :pokeDatos="arrayFiltrado[index].descripcion"
        :imagen="arrayFiltrado[index].imagen"
        :movimiento="arrayFiltrado[index].movimiento"
        :altura="arrayFiltrado[index].altura"
        :peso="arrayFiltrado[index].peso"
        :habilidad="arrayFiltrado[index].habilidad"
      />
    </b-container>
  </div>
</template>
<script>
import PokemonAtrapado from "@/components/PokemonAtrapado.vue";
import { mapState } from "vuex";

export default {
  name: "Entrenador",
  components: {
    PokemonAtrapado,
  },
  data() {
    return {
      entrenador: this.$route.params.n_entrenador,
      arrayFiltrado: [],
    };
  },
  computed: {
    ...mapState(["arrayPokemones"]),
  },
  methods: {
    filtrar: function () {
      console.log(this.entrenador);
      let arrayFiltrado = this.arrayPokemones.filter(
        (el) => el.entrenador == this.entrenador
      );
      this.arrayFiltrado = arrayFiltrado;
      console.log(arrayFiltrado);
    },
  },
  created() {
    this.filtrar();
  },
  watch: {
    $route() {
      this.entrenador = this.$route.params.n_entrenador;
      this.filtrar();
    },
  },
};
</script>
