import Vue from "vue";
import Vuex from "vuex";
import axios from "axios"
import router from "../router";
import firebase from "firebase/app"


Vue.use(Vuex);


export default new Vuex.Store({
  state: {
    pokemon: {
      pokeNombre: "",
      selected: null,
      options: [
        { value: null, text: "Por favor, seleccione un Entrenador" },
        { value: "/EntrenadoraRomina", text: "EntrenadoraRomina" },
        { value: "/EntrenadorMiguel", text: "EntrenadorMiguel" },
        { value: "/EntrenadorLuis", text: "EntrenadorLuis" },
        { value: "/EntrenadoraDaniela", text: "EntrenadoraDaniela" },
        { value: "/EntrenadorAndres", text: "EntrenadorAndres" },
        { value: "/EntrenadorJose", text: "EntrenadorJose" },
        { value: "/EntrenadorEdgar", text: "EntrenadorEdgar" },
      ],
      pokeImagen: "",
      pokeDatos: "",
      move: "",
      altura: "",
      peso: "",
      habilidad: "",
    },
    arrayPokemones: [],
    arrayRutas: [],
    urlBaseApi: "https://pokeapi.co/api/v2/pokemon/",
    form:{
      email:"",
      contrasena:""
    },
    fire:{
      usuario:"",
      pass:""
    },
    token:"",
    mostrar:false
  },
  mutations: {
    login(state) {
      let body = { email: state.form.email, password: state.form.contrasena };
      axios
        .post("https://reqres.in/api/login", body)
        .then((respuesta) => {
          console.log(body)
          console.log(respuesta.data.token);
          router.push("/atraparpokemon");
          state.token = respuesta.data.token;
          state.mostrar = true ; 
        })

        .catch((error) => {
          alert("Credenciales invalidadas , vuelve a ingresarlas " + error.message);
          console.log(body)
        });
    },
    loginFire(state){

      firebase.auth().signInWithEmailAndPassword(state.fire.usuario, state.fire.pass)
      .then((resp) =>{
        router.push("/atraparpokemon");
        state.mostrar = true
        console.log(resp)
      })
      .catch((err)=>{
        alert(err.message);
        console.log(err)
      })

    },
    agregarPoke(state) {
      let objeto = {
        valor: state.pokemon.options.find(
          (el) => el.value == state.pokemon.selected
        ).text,
        path: state.pokemon.options.find(
          (el) => el.value == state.pokemon.selected
        ).value,
      };
      let pokemon = {
        nombre: state.pokemon.pokeNombre,
        entrenador: state.pokemon.options.find(
          (el) => el.value == state.pokemon.selected
        ).text,
        imagen: state.pokemon.pokeImagen,
        descripcion: state.pokemon.pokeDatos,
        movimiento: state.pokemon.move,
        altura: state.pokemon.altura,
        peso: state.pokemon.peso,
        habilidad: state.pokemon.habilidad,
      };
      if (!state.arrayRutas.some((el) => el.valor == objeto.valor)) {
        state.arrayRutas.push(objeto);
      }
      state.arrayPokemones.push(pokemon);
    },
  },
  actions: {
    agregarPokeAction(context) {
      context.commit("agregarPoke");
    },
    loginAction(context){
      context.commit("login")
    },
    loginFireAction(context){
      context.commit("loginFire")
    }
  },
  getters: {
    armarURLPokemon: function (state) {
      let urlArmada = state.urlBaseApi + state.pokemon.pokeNombre.toLowerCase();
      return urlArmada;
    },
  },
});
